 .App {
  text-align: center;
}
ul{
  color: #61dafb;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
body {
  background: linear-gradient(-45deg, #C7EAE4, #A7E8BD, #FCBCB8, #EFA7A7);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
}

@keyframes gradient {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* New Add */

footer {
  position: center;
  text-align: center;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}

footer p {
  color: black;
}

body {
  position: center;
  text-align: center;
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column; /* Stack elements vertically */
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0; /* Background color */
  background-color: #007bff;
}

img {
  max-width: 400%; /* Make sure the image is responsive */
  height:auto;
  width: 100%;
  margin-top: 20px; /* Add margin to the top of the image */
  position: center;
}
.container {

  text-align: center;
  background: white;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  max-width: 500px; /* Limit max width */
  width: 100%;
  /* margin-top: 20px; Ensure there's space between the image and container */
}
/* h1 {
  color: #333;
} */
.stars {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}
.star {
  font-size: 40px;
  cursor: pointer;
  color: #ccc;
}
.star:hover,
.star.selected {
  color: #FFD700;
}
#responseMessage {
  margin-top: 20px;
  color: #f44336;
}
#feedbackForm {
  /* display: none; */
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
}
textarea {
  width: 100%;
  height: 150px; /* Make the textarea bigger */
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}
input[type="text"],
input[type="email"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}
button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #4CAF50;
  color: white;
  cursor: pointer;


}
button:hover {
  background-color: #45a049;
}

.checkbox-container {
  display: flex;
  flex-wrap: wrap; /* Allows wrapping into columns */
  gap: 10px; /* Space between items */
  max-width: 400px; /* Set a maximum width for container */
  /* justify-content: center; */
  margin: 0 auto; /* Center the container horizontally */
}

.checkbox-label {
  display: flex;
  align-items: center;
  width: 45%; /* Set each label to take up about half the width */
  font-size: 1.1em;
  cursor: pointer;
}

.checkbox-label input {
  margin-right: 10px;
  accent-color: #007bff;
}

.checkbox-label:hover {
  background-color: #f0f0f0;

}
img{
  /* padding: 20px; */
