  border-radius: 10px; */
  min-width: 300px;
}
/* body */

.review-box {
  min-width: 300px;
  /* height: 150px; */
  /* margin: 0 auto; */
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  overflow: hidden;
  background-color: white;
  max-width: 500px;
}

.review-content {
  opacity: 1;
  transition: opacity 1s ease-in-out;
  word-wrap: break-word; /* Allow long words to break and wrap to next line */
  word-break: break-word; /* Ensure long words donâ€™t overflow the box */
  white-space: normal; /* Allow text to wrap normally */
}

.review-box.fade-out .review-content {
  opacity: 0;
}
